import React from "react";
import Layout from "../components/Layouts/Layout";
import { Link } from "react-router-dom";

const PageNotFound = () => {
  return (
    <Layout title={"Page not available"}>
      <div className=" h-auto bg-white  p-8 rounded text-center">
        <h1 className="text-8xl font-bold text-gray-800">404</h1>
        <p className="text-xl text-gray-600">Page Not Found</p>
        <Link
          to="/"
          className="mt-4 inline-block bg-sky-500 hover:bg-sky-600 text-white font-bold py-2 px-4 rounded"
        >
          Go Back to Home
        </Link>
      </div>{" "}
    </Layout>
  );
};

export default PageNotFound;
