import React, { useEffect, useState } from "react";
import Layout from "../../components/Layouts/Layout";
import UserMenu from "../../components/Layouts/UserMenu";
import { useAuth } from "../../context/auth";
import axios from "axios";
import toast from "react-hot-toast";
const Dashboard = () => {
  const [auth, setAuth] = useAuth();

  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [phone, setPhone] = useState("");
  const [address, setAddress] = useState("");

  useEffect(() => {
    const { email, name, phone, address } = auth?.user;
    setName(name);
    setPhone(phone);
    setEmail(email);
    setAddress(address);
  }, [auth?.user]);

  //form submit
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.put(
        `${process.env.REACT_APP_API}/api/v1/auth/update-profile`,
        { name, email, password, phone, address }
      );
      if (data?.error) {
        toast.error(data?.message);
      } else {
        setAuth({ ...auth, user: data?.updatedUser });
        openEditForm();
        let ls = localStorage.getItem("auth");
        ls = JSON.parse(ls);
        ls.user = data.updatedUser;
        localStorage.setItem("auth", JSON.stringify(ls));
        toast.success(data?.message);
      }
    } catch (error) {
      console.log(error);
      toast.error("Something went Wrong");
    }
  };

  const openEditForm = () => {
    const EditForm = document.getElementById("EditForm");
    EditForm.classList.toggle("hidden");
  };

  return (
    <Layout title={"Admin - Dashboard"}>
      <div className=" ">
        <div className=" grid grid-cols-10">
          <div className=" col-span-2">
            <UserMenu></UserMenu>
          </div>
          <main className="col-span-8 p-8 flex flex-col items-center">
            <div className="bg-slate-200  w-2/3 lg:w-1/3 m-auto p-8 pb-4 my-16 rounded-lg shadow-lg">
              <div className="flex justify-center  -mt-14">
                <img
                  src="/images/man2.png "
                  alt="Profile"
                  className="rounded-full border-4 pt-5 border-slate-200 w-60"
                />
              </div>
              <div className="text-center mt-4">
                <h1 className="text-2xl font-semibold">{auth?.user?.name}</h1>
              </div>
              <div className="mt-4">
                <p className="text-gray-700">Email: {auth?.user?.email}</p>
                <p className="text-gray-700">Phone: {auth?.user?.phone}</p>
              </div>
              <a href="#EditForm">
                <div
                  className=" cursor-pointer mt-3 text-center text-sky-600 text-lg font-semibold hover:scale-110 hover:text-emerald-500 hover:bg-gray-100 rounded-lg"
                  onClick={() => {
                    openEditForm();
                  }}
                >
                  Edit 🖊
                </div>
              </a>
            </div>
            {/* form */}
            <div
              id="EditForm"
              className="bg-slate-200 p-7 hidden rounded-lg shadow-md w-96"
            >
              <h1 className="text-2xl font-semibold mb-6">Edit Profile</h1>

              <form onSubmit={handleSubmit}>
                {/* name */}
                <div className="mb-4">
                  <label
                    htmlFor="name"
                    className="block text-gray-700 font-bold mb-2"
                  >
                    Name
                  </label>
                  <input
                    type="text"
                    id="name"
                    name="name"
                    className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:border-sky-500"
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    required
                    placeholder="Your Name"
                  />
                </div>
                {/* email */}
                <div className="mb-4">
                  <label
                    htmlFor="email"
                    className="block text-gray-700 font-bold mb-2"
                  >
                    Email
                  </label>
                  <input
                    type="email"
                    id="email"
                    name="email"
                    className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:border-sky-500"
                    placeholder="example@mail.com"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    required
                    disabled
                  />
                </div>
                {/* Password */}
                <div className="mb-4">
                  <label
                    htmlFor="password"
                    className="block text-gray-700 font-bold mb-2"
                  >
                    Password
                  </label>
                  <input
                    type="password"
                    id="password"
                    name="password"
                    className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:border-sky-500"
                    placeholder="********"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    required
                  />
                </div>
                {/* Phone */}
                <div className="mb-4">
                  <label
                    htmlFor="phone"
                    className="block text-gray-700 font-bold mb-2"
                  >
                    Phone Number
                  </label>
                  <input
                    type="text"
                    id="phone"
                    name="phone"
                    className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:border-sky-500"
                    placeholder=""
                    value={phone}
                    onChange={(e) => setPhone(e.target.value)}
                    required
                  />
                </div>
                {/* Phone */}
                <div className="mb-4">
                  <label
                    htmlFor="phone"
                    className="block text-gray-700 font-bold mb-2"
                  >
                    Address
                  </label>
                  <input
                    type="text"
                    id="phone"
                    name="phone"
                    className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:border-sky-500"
                    placeholder="Delhi, India"
                    value={address}
                    onChange={(e) => setAddress(e.target.value)}
                    required
                  />
                </div>

                {/* submit */}
                <div className="flex justify-between items-center">
                  <button
                    type="submit"
                    className="bg-blue-500 text-white px-4 py-2 rounded-lg hover:bg-blue-600 active:bg-sky-400 active:scale-95"
                  >
                    Update
                  </button>
                </div>
              </form>
            </div>
          </main>
        </div>
      </div>
    </Layout>
  );
};

export default Dashboard;
