import React, { useState, useEffect } from "react";
import Layout from "../../components/Layouts/Layout";
import AdminMenu from "../../components/Layouts/AdminMenu";
import toast from "react-hot-toast";
import axios from "axios";
import { useNavigate } from "react-router-dom";

const CreateProduct = () => {
  const navigate = useNavigate();
  const [categories, setCategories] = useState([]);
  const [photo, setPhoto] = useState("");
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [price, setPrice] = useState("");
  const [category, setCategory] = useState("");
  const [quantity, setQuantity] = useState("");
  const [shipping, setShipping] = useState("");
  const [ship, setShip] = useState("");
  const [catName, setCatName] = useState("");

  //search bar focus
  const searchList = document.getElementById("search-list");
  const shippingBar = document.getElementById("shipping-bar");
  const searchItems = document.querySelectorAll("#searchIDs");

  const searchBarFocus = () => {
    searchList.classList.toggle("hidden");
  };

  const shippingBarFocus = () => {
    shippingBar.classList.toggle("hidden");
  };

  const handleCreateProduct = async (e) => {
    e.preventDefault();
    try {
      const productData = new FormData();
      productData.append("name", name);
      productData.append("description", description);
      productData.append("price", price);
      productData.append("category", category);
      productData.append("quantity", quantity);
      productData.append("photo", photo);
      productData.append("shipping", shipping);

      const { data } = await axios.post(
        `${process.env.REACT_APP_API}/api/v1/product/create-product`,
        productData
      );
      if (data?.success) {
        navigate("/dashboard/admin/products");
        toast.success("Product Created Successfully ");
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong");
    }
  };

  const searchFilter = () => {
    const data = category.toLowerCase();
    searchItems.forEach((item) => {
      const text = item.textContent.toLowerCase();
      const match = text.includes(data);
      item.style.display = match ? "block" : "none";
    });
  };

  useEffect(() => {
    searchFilter();
  }, [category]);

  //get all categories
  const getAllCategory = async () => {
    try {
      const { data } = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/category/get-category`
      );
      if (data?.success) {
        setCategories(data?.category);
      }
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong");
    }
  };

  useEffect(() => {
    getAllCategory();
  }, []);

  return (
    <Layout title={"Admin - Products"}>
      <div className=" container">
        <div className=" grid grid-cols-10">
          <div className=" col-span-2">
            <AdminMenu></AdminMenu>
          </div>
          {/* content */}

          <main className="col-span-8 p-4 w-full mx-auto">
            <div className="container mx-auto p-6">
              {/* header */}
              <div className="  shadow-lg flex rounded-lg justify-between items-center px-6 py-4 mb-2">
                <h1 className="text-2xl w-full  font-semibold">
                  New Products{" "}
                </h1>
              </div>

              <div className="w-full pt-1 bg-slate-100 mt-6 rounded">
                <div className="mx-4 my-6">
                  <h2 className="text-lg font-semibold text-gray-800">
                    Enter Product Details
                  </h2>
                </div>
                {/* Photo */}
                <div className="  h-16  relative  w-auto h mx-auto">
                  <div className="w-full px-8  absolute flex bottom-0 items-end justify-between space-x-2">
                    <div className="flex space-x-5">
                      <label
                        htmlFor="fileInput"
                        className="cursor-pointer bg-blue-500 hover:bg-blue-700 text-white font-semibold py-2 px-4  rounded inline-block text-center"
                      >
                        Add Product Image
                      </label>
                      <p className="  text-base bg-gray-200 text-indigo-500 text-center truncate rounded font-semibold px-4 py-2">
                        {photo ? "✓ " + photo.name : "✘"}
                      </p>
                      <input
                        type="file"
                        id="fileInput"
                        className="hidden"
                        accept="image/*"
                        onChange={(e) => {
                          setPhoto(e.target.files[0]);
                        }}
                      />
                    </div>
                    <div
                      className=" rounded-lg overflow-hidden"
                      style={{ height: "120px", minWidth: "150px" }}
                    >
                      {photo && (
                        <img
                          className="w-auto h-full "
                          src={
                            URL.createObjectURL(photo)
                              ? URL.createObjectURL(photo)
                              : "/images/man1.png"
                          }
                          alt="photos here"
                        />
                      )}
                    </div>
                  </div>
                </div>
                {/* photo */}

                {/* <!-- Form --> */}
                <form
                  onSubmit={handleCreateProduct}
                  className="  shadow-md rounded px-8 pt-6 pb-8 mb-4"
                >
                  {/* Select Category */}
                  <div
                    id="search-box"
                    className=" mb-4 relative container mx-auto "
                  >
                    <label
                      className="block text-gray-700 text-sm font-bold mb-2"
                      htmlFor="name"
                    >
                      Category
                    </label>
                    <input
                      type="text"
                      onFocus={searchBarFocus}
                      placeholder="Select a Category..."
                      className="w-full px-4 py-1 my-1 outline outline-gray-300 outline-2  rounded text-gray-700 focus:outline-sky-400"
                      value={catName}
                      onChange={(e) => {
                        setCategory(e.target.value);
                      }}
                    />
                    <div
                      id="search-list"
                      className="absolute overflow-hidden hidden z-10 mt-2 bg-gray-300 shadow-md rounded w-full"
                    >
                      <ul>
                        {categories?.map((c) => {
                          return (
                            <li
                              id="searchIDs"
                              key={c._id}
                              onClick={() => {
                                setCategory(c._id);
                                setCatName(c.name);
                                searchBarFocus();
                              }}
                              className="px-4 py-2 cursor-pointer hover:bg-sky-600 hover:text-white"
                            >
                              {c.name}
                            </li>
                          );
                        })}
                      </ul>
                    </div>
                  </div>
                  {/* Select end */}
                  {/* name */}
                  <div className="mb-4">
                    <label
                      className="block text-gray-700 text-sm font-bold mb-2"
                      htmlFor="name"
                    >
                      Name
                    </label>
                    <input
                      className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight outline outline-0 focus:outline-2 focus:outline-sky-400"
                      id="name"
                      type="text"
                      placeholder="Product Name"
                      onChange={(e) => setName(e.target.value)}
                      value={name}
                    />
                  </div>

                  {/* description */}
                  <div className="mb-4">
                    <label
                      className="block text-gray-700 text-sm font-bold mb-2"
                      htmlFor="description"
                    >
                      Description
                    </label>
                    <textarea
                      className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight outline outline-0 focus:outline-2 focus:outline-sky-400"
                      id="description"
                      type="text"
                      placeholder="About Product"
                      onChange={(e) => setDescription(e.target.value)}
                      value={description}
                    />
                  </div>

                  {/* Price */}
                  <div className="mb-4">
                    <label
                      className="block text-gray-700 text-sm font-bold mb-2"
                      htmlFor="price"
                    >
                      Price
                    </label>
                    <input
                      className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight outline outline-0 focus:outline-2 focus:outline-sky-400"
                      id="price"
                      type="text"
                      placeholder="Product Price"
                      onChange={(e) => setPrice(e.target.value)}
                      value={price}
                    />
                  </div>

                  {/* quantity */}
                  <div className="mb-4">
                    <label
                      className="block text-gray-700 text-sm font-bold mb-2"
                      htmlFor="price"
                    >
                      Quantity
                    </label>
                    <input
                      className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight outline outline-0 focus:outline-2 focus:outline-sky-400 "
                      id="quantity"
                      type="text"
                      placeholder="Product Quantity"
                      onChange={(e) => setQuantity(e.target.value)}
                      value={quantity}
                    />
                  </div>

                  {/* Shipping */}
                  <div className="mb-4 relative">
                    <label
                      className="block text-gray-700 text-sm font-bold mb-2"
                      htmlFor="shipping"
                    >
                      Shipping
                    </label>
                    <input
                      className="shadow appearance-none border rounded w-full py-2 px-3 font-semibold text-gray-700 leading-tight outline outline-0 focus:outline-2 focus:outline-sky-400 cursor-pointer"
                      id="shipping"
                      type="text"
                      placeholder="Product Shipping"
                      value={ship}
                      readOnly
                      onClick={shippingBarFocus}
                    />
                    <div id="shipping-bar" className="hidden">
                      <ul className="absolute  overflow-hidden rounded-lg w-full bg-gray-300">
                        <li
                          onClick={() => {
                            setShipping(1);
                            shippingBarFocus();
                            setShip("YES");
                          }}
                          className="px-4 py-2 cursor-pointer hover:bg-sky-600 hover:text-white"
                        >
                          Yes
                        </li>
                        <li
                          onClick={() => {
                            setShipping(0);
                            shippingBarFocus();
                            setShip("NO");
                          }}
                          className="px-4 py-2 cursor-pointer hover:bg-sky-600 hover:text-white"
                        >
                          No
                        </li>
                      </ul>
                    </div>
                  </div>

                  <div className="mb-4">
                    <button
                      className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
                      type="submit"
                    >
                      Add Product
                    </button>
                  </div>
                </form>
                {/* form */}
              </div>
            </div>
          </main>
        </div>
      </div>
    </Layout>
  );
};

export default CreateProduct;
