import React, { useState } from "react";
import { Link, NavLink } from "react-router-dom";
import { useAuth } from "../../context/auth";
import { useCart } from "../../context/cart";
import toast from "react-hot-toast";

const Header = () => {
  const [auth, setAuth] = useAuth();
  const [cart] = useCart();

  const handleLogout = () => {
    setAuth({
      ...auth,
      user: null,
      token: "",
    });
    localStorage.removeItem("auth");
    toast.success("Logout Successfully");
  };

  const mobileMenuClick = () => {
    const mobileMenu = document.getElementById("mobile-menu");
    mobileMenu.classList.toggle("hidden");
  };

  const navDropdown = () => {
    const Dropdown = document.getElementById("navDropdown");
    Dropdown.classList.toggle("hidden");
  };

  return (
    <>
      <nav className="bg-gray-800 w-screen p-6 shadow-xl z-50 ">
        <div className="container mx-auto flex justify-between items-center">
          <Link to="/" className="text-white text-2xl font-bold cursor-pointer">
            WearWave
          </Link>

          <div className="hidden md:flex space-x-7">
            <NavLink to="/" className="  text-white hover:text-stone-300">
              Home
            </NavLink>

            {/* <NavLink
              to="/catagory"
              className="  text-white hover:text-stone-300"
            >
              Catagory
            </NavLink> */}
            <NavLink to="/shop" className="  text-white hover:text-stone-300">
              Shop
            </NavLink>
            <NavLink
              to="/cart"
              className=" relative  text-white hover:text-stone-300 bg-red-30"
            >
              Cart
              {cart.length > 0 ? (
                <div className="absolute bg-rose-500 p-3 flex justify-center items-center  rounded-full -top-4 -right-4">
                  <p className="absolute">{cart?.length}</p>
                </div>
              ) : (
                ""
              )}
            </NavLink>

            {!auth.user ? (
              <>
                <NavLink
                  to="/register"
                  className=" text-white hover:text-stone-300"
                >
                  Register
                </NavLink>
                <NavLink
                  to="/login"
                  className="  text-white hover:text-stone-300"
                >
                  Login
                </NavLink>
              </>
            ) : (
              <>
                {/* dropdown */}
                <div
                  onClick={navDropdown}
                  data-dropdown-trigger="hover"
                  className=" relative inline-block cursor-pointer"
                >
                  <div className=" text-lg inline-flex text-slate-400  hover:border-white hover:text-green-400  border-l-2 rounded border-transparent px-1 border-r-2">
                    <button type="button" className="px-1">
                      {auth?.user?.name}
                    </button>
                    <p className="px-1">🢛</p>
                  </div>

                  <div
                    id="navDropdown"
                    className="hidden absolute right-0 mt-3 min-w-full rounded shadow-lg text-end bg-slate-600 overflow-hidden "
                    role="menu"
                    aria-labelledby="options-menu"
                  >
                    <div className=" z-50 text-white" role="none">
                      <Link
                        to={`/dashboard/${
                          auth?.user?.role === 1
                            ? "admin/profile"
                            : "user/profile"
                        }`}
                        className="block  py-2 px-4 hover:text-sky-400 hover:bg-slate-700"
                        role="menuitem"
                      >
                        Dashboard
                      </Link>
                      <Link
                        to="/login"
                        onClick={handleLogout}
                        className="block  py-2 px-4 hover:text-sky-400 hover:bg-slate-700"
                        role="menuitem"
                      >
                        Logout
                      </Link>
                    </div>
                  </div>
                </div>
                {/* dropdown ends */}
              </>
            )}
          </div>

          {/* Three bars for mobile screen */}
          <div className="md:hidden">
            <button
              id="mobile-menu-button"
              onClick={() => {
                mobileMenuClick();
              }}
              className="text-white"
            >
              <svg
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
                className="h-6 w-6"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M4 6h16M4 12h16M4 18h16"
                ></path>
              </svg>
            </button>
          </div>
        </div>
      </nav>

      {/* mobile screen */}
      <div
        id="mobile-menu"
        className="md:hidden w-screen absolute hidden bg-gray-700 "
      >
        <ul className=" py-4  text-lg flex-col space-y-3 ">
          <li className=" flex justify-center">
            <NavLink
              to="/"
              className="nav_link text-white hover:text-stone-300"
            >
              Home
            </NavLink>
          </li>

          <li className="flex justify-center">
            <NavLink
              to="/catagory"
              className="nav_link text-white hover:text-stone-300"
            >
              Catagory
            </NavLink>
          </li>
          {!auth.user ? (
            <>
              <li className=" flex justify-center">
                <NavLink to="/register" className="nav_link text-white ">
                  Register
                </NavLink>
              </li>
              <li className=" flex justify-center">
                <NavLink
                  to="/login"
                  className="nav_link  text-white hover:text-stone-300"
                >
                  Login
                </NavLink>
              </li>
            </>
          ) : (
            <>
              <li className="z-50 flex justify-center">
                <NavLink
                  to="/login"
                  onClick={handleLogout}
                  className=" nav_link text-white hover:text-stone-300"
                >
                  Logout
                </NavLink>
              </li>
              <li className=" flex justify-center">
                <NavLink
                  to={`/dashboard/${
                    auth?.user?.role === 1 ? "admin/profile" : "user/profile"
                  }`}
                  className=" nav_link text-white hover:text-stone-300"
                >
                  Dashboard
                </NavLink>
              </li>
            </>
          )}
          <li className=" flex justify-center">
            <NavLink
              to="/cart"
              className="nav_link  text-white hover:text-stone-300"
            >
              Cart (0)
            </NavLink>
          </li>
        </ul>
      </div>
    </>
  );
};

export default Header;
