import React, { useEffect, useState } from "react";
import Layout from "../components/Layouts/Layout";
import { useCart } from "../context/cart";
import { useAuth } from "../context/auth";
import { useNavigate } from "react-router-dom";
import toast from "react-hot-toast";
import DropIn from "braintree-web-drop-in-react";
import axios from "axios";

const CartPage = () => {
  const [auth, setAuth] = useAuth();
  const [cart, setcart] = useCart();
  const [clientToken, setClientToken] = useState("");
  const [instance, setInstance] = useState("");
  const [checkout, setCheckout] = useState(false);
  const navigate = useNavigate();

  const totalPrice = () => {
    try {
      let total = 0;
      cart?.map((item) => {
        total = total + item.price;
      });
      return total.toFixed(2);
    } catch (error) {
      console.log(error);
    }
  };

  //remove item
  const removeCartItem = (pid) => {
    try {
      let myCart = [...cart];
      let index = myCart.findIndex((item) => item._id === pid);
      myCart.splice(index, 1);
      localStorage.setItem("cart", JSON.stringify(myCart));
      setcart(myCart);
    } catch (error) {
      console.log(error);
    }
  };

  //get payment ayway token
  const getToken = async () => {
    try {
      const { data } = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/product/braintree/token`
      );

      setClientToken(data?.clientToken);
    } catch (error) {
      console.log(error);
    }
  };

  //handle payment
  const handlepayment = async () => {
    try {
      // setLoading(true);
      const { nonce } = await instance.requestPaymentMethod();
      setcart([]);
      localStorage.removeItem("cart");
      navigate("/");
      // if (data?.success) {
      toast.success("Payment Completed Successfully");
      // }
      const { data } = await axios.post(
        `${process.env.REACT_APP_API}/api/v1/product/braintree/payment`,
        { cart, nonce }
      );
    } catch (error) {
      console.log(error);
    }
  };

  //show payment gatway
  const togglePayment = () => {
    const paymentPortal = document.getElementById("payment-portal");
    setCheckout(true);
    paymentPortal.classList.toggle("hidden");
  };

  useEffect(() => {
    getToken();
  }, [auth?.token]);

  return (
    <Layout>
      <main className=" z-0  mx-auto pt-8">
        <div className=" flex flex-col items-center  gap-3 mb-16">
          <h1 className="text-2xl font-semibold ">{`Hi ${
            auth?.token && auth?.user?.name
          }`}</h1>
          <p className="italic ">
            {cart?.length > 0
              ? `You have ${cart.length} items in your cart. ${
                  auth?.token ? "" : "Please Login to checkout"
                }`
              : "Your Cart is empty"}
          </p>
        </div>
        {/* cart grid page */}
        <div className="grid grid-cols-1 md:grid-cols-10 col-span-10 ">
          {/* <!-- Cart Items --> */}
          <div className="col-span-6 px-10 group mb-5">
            <h2 className="text-2xl font-semibold mb-4">Your Cart</h2>
            <div className=" rounded-lg  overflow-hidden ">
              {cart?.map((c, i) => {
                return (
                  <div
                    key={i + c._id}
                    className=" bg-slate-100 flex items-center justify-between rounded-lg p-4  shadow-md hover:bg-sky-100 mb-2"
                  >
                    <img
                      src={`${process.env.REACT_APP_API}/api/v1/product/get-product-photo/${c?._id}`}
                      alt="Product"
                      className="w-4/12  object-cover"
                    />
                    <div className="flex-1 ml-4">
                      <h3 className="text-lg font-semibold">{c?.name}</h3>
                      <p className="text-gray-600">${c?.price}</p>
                    </div>
                    <div className="flex items-center">
                      <button
                        onClick={() => removeCartItem(c?._id)}
                        className="px-4 py-2  bg-gray-300 group-hover:bg-red-500 font-semibold text-red-400 group-hover:text-white 
                        scale-95 rounded hover:scale-100 transition"
                      >
                        Remove
                      </button>
                    </div>
                    {/* <div className="flex items-center">
                      <button className="bg-gray-200 hover:bg-gray-300 text-gray-600 px-2 py-1 rounded">
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          fill="none"
                          viewBox="0 0 24 24"
                          stroke="currentColor"
                          className="w-4 h-4"
                        >
                          <path
                            stroke-linecap="round"
                            stroke-linejoin="round"
                            stroke-width="2"
                            d="M20 12H4"
                          />
                        </svg>
                      </button>
                      <span className="mx-2">1</span>
                      <button className="bg-gray-200 hover:bg-gray-300 text-gray-600 px-2 py-1 rounded">
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          fill="none"
                          viewBox="0 0 24 24"
                          stroke="currentColor"
                          className="w-4 h-4"
                        >
                          <path
                            stroke-linecap="round"
                            stroke-linejoin="round"
                            stroke-width="2"
                            d="M12 6v6m0 0v6m0-6h6m-6 0H6"
                          />
                        </svg>
                      </button>
                    </div> */}
                    <hr></hr>
                  </div>
                );
              })}
              {/* <!-- Repeat cart item structure for each item in the cart --> */}
            </div>
          </div>

          {/* <!-- Cart Summary --> */}
          <div className="col-span-4 px-16 group">
            <h2 className="text-2xl font-semibold mb-4">Order Summary</h2>
            <div className="bg-slate-100 rounded-lg shadow-lg p-4 ">
              <p className=" bg-emerald-100 p-3 rounded text-xl font-semibold mb-5 group-hover:bg-emerald-200">
                Total : ${totalPrice()}
              </p>

              {/* <button className="w-full bg-sky-500 hover:bg-sky-600 text-white font-bold py-2 px-4 rounded">
                Checkout
              </button> */}

              <hr></hr>
              {auth?.user?.address ? (
                <div className="my-4">
                  <div className="flex items-center gap-3 mb-4">
                    <p className=" font-medium text-lg ">Current Address :</p>
                    <p className=" font-semibold text-lg rounded">
                      {auth?.user?.address}
                    </p>
                  </div>
                  <button
                    onClick={() => navigate("/dashboard/user/profile")}
                    className="bg-gray-400 group-hover:bg-sky-500 hover:bg-sky-600 text-white font-bold py-2 px-4 rounded"
                  >
                    Update Address
                  </button>
                </div>
              ) : (
                <div>
                  {auth?.token ? (
                    <button
                      onClick={() => navigate("/dashboard/user/profile")}
                      className=" bg-sky-500 hover:bg-sky-600 text-white font-bold py-2 px-4 rounded"
                    >
                      Update Address
                    </button>
                  ) : (
                    <div className=" space-y-4">
                      <p>Not Logged In </p>
                      <button
                        onClick={() =>
                          navigate("/login", {
                            state: "/cart",
                          })
                        }
                        className=" bg-sky-500 hover:bg-sky-600 text-white font-bold py-2 px-4 rounded"
                      >
                        Login to checkOut
                      </button>
                    </div>
                  )}
                </div>
              )}
              <hr></hr>
              <div className=" mt-4 w-full bgred-300">
                {!clientToken ? (
                  <div className=" flex justify-center text-center">
                    <div className="  border-solid border-sky-500  border-b-2 animate-spin rounded-full mb-3 w-9 h-9"></div>
                  </div>
                ) : (
                  <div id="payment-portal" className="hidden  h-min">
                    {!auth?.user?.address || !clientToken || !cart.length ? (
                      // <p className="text-center mb-3">Not Signed In</p>
                      ""
                    ) : (
                      <div className="-mt-7">
                        <DropIn
                          options={{
                            authorization: clientToken,
                            // paypal: {
                            //   flow: "vault",
                            // },
                          }}
                          onInstance={(instance) => {
                            setInstance(instance);
                          }}
                        />
                      </div>
                    )}
                  </div>
                )}
                {!checkout ? (
                  <button
                    onClick={() => {
                      togglePayment();
                    }}
                    className="w-full bg-emerald-400 hover:bg-emerald-500 text-white font-bold py-2 px-4 rounded"
                    disabled={checkout || !auth?.user?.address}
                  >
                    CheckOut
                  </button>
                ) : (
                  <button
                    onClick={() => {
                      handlepayment();
                    }}
                    className="w-full bg-emerald-400 hover:bg-emerald-500 text-white font-bold py-2 px-4 rounded"
                  >
                    Make Payment
                  </button>
                )}
              </div>
            </div>
          </div>
        </div>
      </main>
    </Layout>
  );
};

export default CartPage;
