/* eslint-disable jsx-a11y/alt-text */
import React, { useEffect, useState } from "react";
import Layout from "../components/Layouts/Layout";
import Man0 from "./man0.png";
import { useAuth } from "../context/auth";
import axios from "axios";
import { Link, useNavigate } from "react-router-dom";

const HomePage = () => {
  const navigate = useNavigate();
  const [auth, setAuth] = useAuth();
  const [products, setProducts] = useState([]);
  const [categories, setCategories] = useState([]);

  //get all products
  const getAllProducts = async (e) => {
    // e.preventDefault()
    try {
      const { data } = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/product/get-product`
      );
      setProducts(data.products);
    } catch (error) {
      console.log(error);
    }
  };

  const getCategories = async () => {
    try {
      const { data } = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/category/get-category`
      );
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getAllProducts();
  }, []);

  return (
    <Layout title={"Ecommerce App"}>
      {/* <pre>{JSON.stringify(auth, null, 4)}</pre> */}
      <header className="bg-gray-100 py-16">
        <div className="container mx-auto text-center">
          <h1 className="text-4xl font-semibold">Welcome to Your WearWave</h1>
          <p className="mt-4 text-lg text-gray-700">
            Discover the latest products and trends.
          </p>
          <Link
            to="/shop"
            className="mt-8 inline-block px-6 py-3 bg-sky-500 text-white hover:bg-sky-600 rounded-full transition duration-300"
          >
            Shop Now
          </Link>
        </div>
      </header>

      <section className="py-5 px-6">
        <div className="container  px-10 mx-auto text-center">
          {/* <h2 className="text-2xl font-semibold mb-6">Featured Products</h2> */}
          <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-2">
            {products.map((p) => {
              return (
                <div
                  key={p._id}
                  style={{ width: "340px" }}
                  className="scale-90 flex flex-col rotate items-center justify-start bg-stone-200 my-3 mx-10 max-w-xs rounded pb-10 shadow-md  group hover:shadow-lg shadow-gray-400 hover:shadow-gray-300 hover:bg-gray-100 transition relative"
                >
                  <div
                    style={{ minHeight: "300px" }}
                    className=" w-full relative flex justify-center items-center mt-2"
                  >
                    <img
                      style={{ width: "100%", height: "95%" }}
                      className="admin_product_img rounded absolute bg-red500 shadow-gray-400 mt-3 scale-90  group-hover:scale-100 group-hover:-top-3"
                      src={`${process.env.REACT_APP_API}/api/v1/product/get-product-photo/${p._id}`}
                      // alt={`${p.name}_Picture`}
                    />
                  </div>

                  <div className="w-full px-4 py-1 space-y-3 ">
                    {/* name / description /*/}
                    <div className=" text-start px-5 ">
                      <div className="font-bold text-xl mb-2">{p.name}</div>
                      <div className="w-full  overflow:hidden line-clamp-2">
                        <p className="text-gray-700 text-base">
                          {p.description}
                        </p>
                      </div>
                    </div>
                  </div>
                  {/* button */}
                  <div
                    onClick={() => navigate(`/product/${p.slug}`)}
                    className="bg-sky-500  rounded px-4 py-2 absolute -bottom-4 transition opacity-0 group-hover:opacity-100 hover:scale-110 cursor-pointer shadow shadow-gray-500 hover:shadow-md hover:shadow-gray-300 hover:bg-sky-600 text-white"
                  >
                    <p className="">Buy Now</p>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </section>
    </Layout>
  );
};

export default HomePage;
