import React from "react";
import Layout from "../../components/Layouts/Layout";
import UserMenu from "../../components/Layouts/UserMenu";

const Orders = () => {
  return (
    <Layout title={"Admin - Products"}>
      <div className=" grid grid-cols-10">
        <div className=" col-span-2  bg-green-500">
          <UserMenu></UserMenu>
        </div>
        {/* content */}
        <main className="col-span-8 p-4">
          <div className="  shadow-lg flex rounded-lg justify-between items-center px-6 py-4 mb-3">
            <h1 className="text-2xl w-fit  font-semibold">Your Orders</h1>
          </div>
          <div className="my-10">
            <h1 className="text-lg text-center animate-pulse ">
              This may take sometime ...
            </h1>
          </div>
        </main>
      </div>
    </Layout>
  );
};

export default Orders;
