import React from "react";
import Layout from "../components/Layouts/Layout";

const Contact = () => {
  return (
    <Layout title={"Contact us"}>
      <div className="bg-slate-100">
        <div className="container w-10/12 m-auto p-6">
          <h1 className="text-3xl font-semibold text-gray-800 mb-6">
            Contact Us
          </h1>

          {/* <!-- Contact Information --> */}
          <div className=" bg-white rounded-lg shadow-md p-6 mb-6">
            <h2 className="text-xl font-semibold text-gray-800 mb-4">
              Contact Information
            </h2>
            <p className="text-gray-600">
              Feel free to reach out to us through the following contact
              information:
            </p>

            <ul className="mt-4">
              <li className="flex items-center mb-2">
                Email:
                <a
                  href="mailto:sanketjaswal772@gmail.com"
                  className="text-blue-600 mx-3 hover:underline"
                >
                  sanket.jaswal1234@gmail.com
                </a>
              </li>
              <li className="flex items-center">
                Phone:{" "}
                <span className="text-gray-600 mx-3"> (+91) 8894951224</span>
              </li>
            </ul>
          </div>

          {/* <!-- Contact Form (if needed) --> */}
          <div className="bg-white rounded-lg shadow-md p-6">
            <h2 className="text-xl font-semibold text-gray-800 mb-4">
              Send us a message
            </h2>
            <form>
              <div className="mb-4">
                <label htmlFor="name" className="block text-gray-600">
                  Your Name
                </label>
                <input
                  type="text"
                  id="name"
                  name="name"
                  className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:border-blue-500"
                  required
                />
              </div>
              <div className="mb-4">
                <label htmlFor="email" className="block text-gray-600">
                  Your Email
                </label>
                <input
                  type="email"
                  id="email"
                  name="email"
                  className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:border-blue-500"
                  required
                />
              </div>
              <div className="mb-4">
                <label htmlFor="message" className="block text-gray-600">
                  Message
                </label>
                <textarea
                  id="message"
                  name="message"
                  className="w-full px-4 py-2 border rounded-lg resize-none focus:outline-none focus:border-blue-500"
                  rows="4"
                  required
                ></textarea>
              </div>
              <button
                type="submit"
                className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600 focus:outline-none"
              >
                Send Message
              </button>
            </form>
          </div>
        </div>{" "}
      </div>
    </Layout>
  );
};

export default Contact;
