import React from "react";

const CategoryForm = ({ handleSubmit, value, setValue, btnTxt }) => {
  return (
    <div className="container bg-white ">
      <form className="flex items-center space-x-5" onSubmit={handleSubmit}>
        <div className="  flex items-center  space-x-5">
          <input
            type="text"
            id="name"
            name="name"
            className=" w-full  outline-none border-2 rounded py-1 px-3 text-gray-700 focus:border-sky-400"
            value={value}
            placeholder={`   ${btnTxt} Category . . .`}
            onChange={(e) => setValue(e.target.value)}
          />
        </div>

        <div>
          <button
            type="submit"
            className="bg-sky-400 hover:bg-sky-700 text-white font-bold py-2 px-3 rounded"
          >
            {btnTxt}
          </button>
        </div>
      </form>
    </div>
  );
};

export default CategoryForm;
