/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState } from "react";
import Layout from "../../components/Layouts/Layout";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import toast from "react-hot-toast";

const Register = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [phone, setPhone] = useState("");
  const [address, setAddress] = useState("");
  const [question, setQuestion] = useState("");

  const navigate = useNavigate();

  //form submit
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const res = await axios.post(
        `${process.env.REACT_APP_API}/api/v1/auth/register`,
        { name, email, password, phone, address, question }
      );
      if (res.data && res.data.success) {
        toast.success(res.data.message);
        navigate("/login");
      } else {
        console.log(res.data.message);
        toast.error(res.data.message);
      }
    } catch (error) {
      console.log(error);
      toast.error("Something went Wrong");
    }
  };

  return (
    <Layout title={"Register here"}>
      <div className=" min-h-screen flex items-center justify-center">
        <div className="bg-slate-200 p-7 rounded-lg shadow-md w-96">
          <h1 className="text-2xl font-semibold mb-6">Welcome..</h1>
          <form onSubmit={handleSubmit}>
            {/* name */}
            <div className="mb-4">
              <label
                htmlFor="name"
                className="block text-gray-700 font-bold mb-2"
              >
                Name
              </label>
              <input
                type="text"
                id="name"
                name="name"
                className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:border-sky-500"
                value={name}
                onChange={(e) => setName(e.target.value)}
                required
                placeholder="Your Name"
              />
            </div>
            {/* email */}
            <div className="mb-4">
              <label
                htmlFor="email"
                className="block text-gray-700 font-bold mb-2"
              >
                Email
              </label>
              <input
                type="email"
                id="email"
                name="email"
                className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:border-sky-500"
                placeholder="example@mail.com"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
              />
            </div>
            {/* Password */}
            <div className="mb-4">
              <label
                htmlFor="password"
                className="block text-gray-700 font-bold mb-2"
              >
                Password
              </label>
              <input
                type="password"
                id="password"
                name="password"
                className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:border-sky-500"
                placeholder="********"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
              />
            </div>
            {/* Phone */}
            <div className="mb-4">
              <label
                htmlFor="phone"
                className="block text-gray-700 font-bold mb-2"
              >
                Phone Number
              </label>
              <input
                type="text"
                id="phone"
                name="phone"
                className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:border-sky-500"
                placeholder=""
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
                required
              />
            </div>
            {/* Phone */}
            <div className="mb-4">
              <label
                htmlFor="phone"
                className="block text-gray-700 font-bold mb-2"
              >
                Address
              </label>
              <input
                type="text"
                id="phone"
                name="phone"
                className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:border-sky-500"
                placeholder="Delhi, India"
                value={address}
                onChange={(e) => setAddress(e.target.value)}
                required
              />
            </div>
            {/* Question */}
            <div className="mb-4">
              <label
                htmlFor="question"
                className="block text-gray-700 font-bold mb-2"
              >
                Name of our pet
              </label>
              <input
                type="text"
                id="question"
                name="question"
                className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:border-sky-500"
                placeholder="Tuffy , Zoro"
                value={question}
                onChange={(e) => setQuestion(e.target.value)}
                required
              />
            </div>
            {/* submit */}
            <div className="flex justify-between items-center">
              <button
                type="submit"
                className="bg-blue-500 text-white px-4 py-2 rounded-lg hover:bg-blue-600 active:bg-sky-400 active:scale-95"
              >
                Register
              </button>
              <a
                onClick={() => navigate("/login")}
                className="text-gray-600 hover:text-sky-500 active:text-purple-500 cursor-pointer"
              >
                Already have an account?
              </a>
            </div>
          </form>
        </div>
      </div>{" "}
    </Layout>
  );
};

export default Register;
