import React from "react";
import { NavLink } from "react-router-dom";
import { useAuth } from "../../context/auth";

const UserMenu = () => {
  const [auth] = useAuth();

  return (
    <>
      <aside
        style={{ minHeight: "76vh" }}
        className=" bg-slate-700 h-full shadow-md text-white p-4 "
      >
        <div className="text-center">
          <h1 className="text-2xl font-semibold">User Panel</h1>
          <p className="mt-2">Welcome, {auth?.user?.name}</p>
        </div>

        <nav className="mt-6">
          <ul>
            <li className="overflow-hidden">
              <NavLink
                to="/dashboard/user/profile"
                className="block py-2 px-4 text-white hover:bg-gray-500"
              >
                Profile
              </NavLink>
            </li>
            <li className="overflow-hidden">
              <NavLink
                to="/dashboard/user/orders"
                className="block py-2 px-4 text-white hover:bg-gray-500"
              >
                Orders
              </NavLink>
            </li>
          </ul>
        </nav>
      </aside>
    </>
  );
};

export default UserMenu;
