/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable jsx-a11y/alt-text */
import React, { useEffect, useState } from "react";
import Layout from "../components/Layouts/Layout";
import axios from "axios";
import { Prices } from "../components/price";
import { useNavigate } from "react-router-dom";
import { useCart } from "../context/cart";
import toast from "react-hot-toast";

const Shop = () => {
  const navigate = useNavigate();
  const [cart, setCart] = useCart();

  const [products, setProducts] = useState([]);
  const [categories, setCategories] = useState([]);
  const [checked, setChecked] = useState([]);
  const [radio, setRadio] = useState([]);
  const [total, setTotal] = useState(0);
  const [page, setPage] = useState(1);
  const [loading, setLoading] = useState(true);

  const [filter, setFilter] = useState(false);

  //get total
  const getTotal = async () => {
    try {
      const { data } = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/product/product-count`
      );
      setTotal(data?.total);
    } catch (error) {
      console.log(error);
    }
  };

  //get all products
  const getAllProducts = async (e) => {
    // e.preventDefault()
    try {
      setLoading(true);
      const { data } = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/product/product-list/${page}`
      );
      setLoading(false);
      setProducts(data.products);
    } catch (error) {
      setLoading(false);

      console.log(error);
    }
  };

  const getCategories = async () => {
    try {
      const { data } = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/category/get-category`
      );
      if (data?.success) {
        setCategories(data?.category);
      }
    } catch (error) {
      console.log(error);
    }
  };

  //get filterd product
  const filterProduct = async () => {
    try {
      const { data } = await axios.post(
        `${process.env.REACT_APP_API}/api/v1/product/product-filters`,
        { checked, radio }
      );
      setProducts(data?.products);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getCategories();
    getTotal();
  }, []);

  useEffect(() => {
    if (!checked.length && !radio.length) getAllProducts();
  }, [checked.length, radio.length]);

  useEffect(() => {
    if (checked.length || radio.length) filterProduct();
  }, [checked, radio]);

  useEffect(() => {
    if (page === 1) return;
    loadMore();
  }, [page]);

  //load more
  const loadMore = async () => {
    try {
      setLoading(true);
      const { data } = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/product/product-list/${page}`
      );
      setProducts([...products, ...data?.products]);
      setLoading(false);
    } catch (error) {
      console.log(error);
    }
  };

  const handleFilter = (value, id) => {
    let all = [...checked];
    if (value) {
      all.push(id);
    } else {
      all = all.filter((c) => c !== id);
    }
    setChecked(all);
  };

  const filterMenuClick = () => {
    const shopFilters = document.getElementById("shop-filters");
    if (filter) {
      shopFilters.style.left = "-50vw";
      setFilter(false);
    } else {
      shopFilters.style.left = "0";
      setFilter(true);
    }
  };

  return (
    <Layout title={"Ecommerce App"}>
      <div className="relative grid grid-cols-6 " style={{ minHeight: "76vh" }}>
        <div
          onClick={filterMenuClick}
          className=" absolute right-5 top-3 p-2 lg:hidden bg-slate-300 rounded flex space-x-2 items-center active:bg-slate-400  "
        >
          <button
            id="mobile-menu-button"
            onClick={() => {
              // mobileMenuClick();
            }}
            className="text-black"
          >
            <svg
              fill="none"
              viewBox="0 0 24 24"
              stroke="currentColor"
              className="h-6 w-6"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M4 6h16M4 12h16M4 18h16"
              ></path>
            </svg>
          </button>
          <h1>Filters</h1>
        </div>
        <div
          id="shop-filters"
          className="z-10 absolute transition-all lg:relative w-1/2 sm:w-1/3 lg:w-full lg:col-span-1 -left-96 lg:left-0 md:flex flex-col gap-5 bg-slate-700 pl-2 h-full"
        >
          <h1 className="text-xl">Fitlers</h1>
          <h4>Filter By Category</h4>
          <div className="space-y-1">
            {categories.map((c) => {
              return (
                <div
                  key={c._id}
                  className="text-gray-300 py-1 hover:bg-slate-600 pl-2"
                >
                  <label
                    className="inline-flex items-center  "
                    onChange={(e) => {
                      handleFilter(e.target.checked, c._id);
                    }}
                  >
                    <input
                      type="checkbox"
                      className="form-checkbox accent-sky-500 h-5 w-5"
                    />
                    <span className="ml-2">{c.name}</span>
                  </label>
                </div>
              );
            })}
          </div>
          <div className=" space-y-2 ">
            <h4>Filter By Price</h4>
            {Prices.map((p) => (
              <div
                key={p._id}
                className="flex items-center pl-2 hover:bg-slate-600 accent-sky-400 rounded-l-full"
              >
                <input
                  id="bordered-radio-2"
                  type="radio"
                  name="bordered-radio"
                  value={p.array}
                  onChange={(e) => {
                    setRadio(p.array);
                  }}
                  className="w-5 h-4 border-white border"
                />
                <label
                  htmlFor="bordered-radio-1"
                  className="w-full py-2 ml-2 text-sm font-medium text-gray-900 dark:text-gray-300 "
                >
                  {p.name}
                </label>
              </div>
            ))}
          </div>
          <div className=" w-full">
            <button
              onClick={() => window.location.reload()}
              className="bg-gray-500 w-11/12 mx-2 rounded px-4 py-2 transition group-hover:opacity-100  cursor-pointer shadow shadow-gray-500 hover:shadow-gray-400 hover:bg-gray-600 text-white "
            >
              Reset Filters
            </button>
          </div>
        </div>
        <main className="mt-16 lg:mt-0 col-span-6 lg:col-span-5 ">
          {/* {JSON.stringify(radio, null, 4)} */}
          <div className="flex shrink-0 flex-wrap justify-center ">
            {products.map((p) => {
              return (
                <div
                  key={p._id}
                  style={{ width: "330px" }}
                  className="lg:scale-75 scale-50 flex flex-col rotate items-center justify-start bg-stone-200  max-w-xs rounded pb-10 shadow-md  group hover:shadow-lg shadow-gray-400 hover:shadow-gray-300 hover:bg-gray-100 transition relative -mx-16 -my-24 sm:-mx-16 lg:-mx-5 lg:-my-8  "
                >
                  <div
                    style={{ minHeight: "300px" }}
                    className=" w-full relative flex justify-center items-center mt-2"
                  >
                    <img
                      style={{ width: "100%", height: "95%" }}
                      className="admin_product_img rounded absolute bg-red500 shadow-gray-400 mt-3 scale-90  group-hover:scale-100 group-hover:-top-3"
                      src={`${process.env.REACT_APP_API}/api/v1/product/get-product-photo/${p._id}`}
                      // alt={`${p.name}_Picture`}
                    />
                  </div>

                  <div className="w-full px-4 py-1 space-y-3 ">
                    {/* name / description /*/}
                    <div className=" text-start px-5 ">
                      <div className="font-bold text-xl mb-2">{p.name}</div>
                      <div className="w-full  overflow:hidden line-clamp-2">
                        <p className="text-gray-700 text-base">
                          {p.description}
                        </p>
                      </div>
                    </div>
                  </div>
                  {/* price */}
                  <div className=" bg-slate-600 rounded-full w-min flex items-center p-9  transition-all  left-4  -top-3 justify-center absolute group-hover:-top-7  group-hover:bg-rose-500 shadow-gray-400 shadow-md group-hover:p-10 group-hover:left-3">
                    <span className=" rounded transition  -rotate-12 font-semibold text-white absolute text-lg group-hover:text-xl">
                      ${p.price}
                    </span>
                  </div>
                  {/* button */}
                  <div
                    onClick={() => {
                      setCart([...cart, p]);
                      localStorage.setItem(
                        "cart",
                        JSON.stringify([...cart, p])
                      );
                      toast.success("Item Added to cart");
                    }}
                    className="bg-sky-500  rounded px-4 py-2 absolute -bottom-4 left-4 transition opacity-0 group-hover:opacity-100 hover:scale-110 cursor-pointer shadow shadow-gray-500 hover:shadow-md hover:shadow-gray-300 hover:bg-sky-600 text-white text-xl"
                  >
                    <p className="">Add to Cart</p>
                  </div>
                  <div
                    onClick={() => navigate(`/product/${p.slug}`)}
                    className="bg-gray-500  rounded px-4 py-2 absolute -bottom-4 right-4 transition opacity-0 group-hover:opacity-100 hover:scale-110 cursor-pointer shadow shadow-gray-500 hover:shadow-md hover:shadow-gray-300 hover:bg-gray-600 text-white text-xl"
                  >
                    <p className="">More Details</p>
                  </div>
                </div>
              );
            })}
          </div>
          <div className="w-full flex justify-center py-5">
            {products && products.length < total && (
              <button
                className="px-4 py-2 mt-0 bg-gray-200 transition active:ml-1 "
                onClick={(e) => {
                  e.preventDefault();
                  setPage(page + 1);
                }}
              >
                {loading ? "Loading..." : "Loadmore"}
              </button>
            )}
          </div>
        </main>
      </div>
    </Layout>
  );
};

export default Shop;
