import React, { useEffect, useState } from "react";
import Layout from "../../components/Layouts/Layout";
import AdminMenu from "../../components/Layouts/AdminMenu";
import toast from "react-hot-toast";
import axios from "axios";
import CategoryForm from "../../components/Form/CategoryForm";

const CreateCategory = () => {
  const [category, setCategory] = useState([]);
  const [name, setName] = useState("");
  const [selected, setSelected] = useState(null);
  const [updatedName, setUpdatedName] = useState("");

  // Toggle Edit Dialog Box

  const toggleEditDialogBox = () => {
    const editBox = document.getElementById("edit-dialog-box");
    editBox.classList.toggle("hidden");
  };

  // Create Category
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.post(
        `${process.env.REACT_APP_API}/api/v1/category/create-category`,
        { name }
      );
      if (data?.success) {
        toast.success(`${name} was created`);
        setName("");
        getAllCategory();
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong in form");
    }
  };

  //Update Category
  const handleUpdate = async (e) => {
    e.preventDefault();
    try {
      if (selected.name === updatedName) {
        toast.error(`${selected.name} already exists`);
        setSelected(null);
        setUpdatedName("");
        toggleEditDialogBox();
      } else {
        const { data } = await axios.put(
          `${process.env.REACT_APP_API}/api/v1/category/update-category/${selected._id}`,
          { name: updatedName }
        );

        if (data?.success) {
          console.log("updated");
          // console.log(data);
          toast.success(` ${selected.name} ⇨ ${data.category.name}`);
          getAllCategory();
          setSelected(null);
          setUpdatedName("");
          toggleEditDialogBox();
        } else {
          toast.error(data.message);
        }
      }
    } catch (error) {
      console.log(error);
      toast.error("something went wrong in form");
    }
  };

  // Delete Category
  const handleDelete = async (id) => {
    try {
      const { data } = await axios.delete(
        `${process.env.REACT_APP_API}/api/v1/category/delete-category/${id}`
      );
      if (data?.success) {
        toast.success(`🗑️Category Removed`);
        getAllCategory();
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      console.log(error);
      toast.error("something went wrong in form");
    }
  };

  //get all categories
  const getAllCategory = async () => {
    try {
      const { data } = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/category/get-category`
      );
      if (data?.success) {
        setCategory(data?.category);
      }
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong");
    }
  };

  useEffect(() => {
    getAllCategory();
  }, []);

  return (
    <Layout title={"Admin - Category"}>
      <div className=" container">
        <div className=" grid grid-cols-10">
          <div className=" col-span-2">
            <AdminMenu></AdminMenu>
          </div>

          {/* dialoag box */}

          <div
            id="edit-dialog-box"
            className="fixed hidden inset-0 flex items-center justify-center z-50 "
          >
            <div className="fixed inset-0 bg-black opacity-50">sdgshdgg</div>
            <div className="relative bg-white rounded-lg shadow-lg p-8">
              <h2 className="text-xl font-semibold mb-4">Edit the Category</h2>
              <div className="flex justify-end">
                <CategoryForm
                  handleSubmit={handleUpdate}
                  value={updatedName}
                  setValue={setUpdatedName}
                  btnTxt={"Edit"}
                />

                <button
                  className="px-4 mx-2 py-2 font-bold bg-gray-300 text-gray-700 rounded hover:text-white hover:bg-gray-400"
                  onClick={toggleEditDialogBox}
                >
                  Cancel
                </button>
              </div>
            </div>
          </div>

          {/* content */}
          <main className="col-span-8 p-4">
            <div className="  container mx-auto p-6">
              <div className="  shadow-lg flex rounded-lg justify-between items-center px-6 py-4 mb-3">
                <h1 className="text-2xl w-fit  font-semibold">
                  Manage Category
                </h1>
                <div className="">
                  <CategoryForm
                    handleSubmit={handleSubmit}
                    value={name}
                    setValue={setName}
                    btnTxt={"Create"}
                  />
                </div>
              </div>
              <table className="mx-auto w-10/12">
                <thead>
                  <tr className="">
                    <th className="px-6 py-3 bg-gray-50 text-left text-xs leading-4 font-medium text-gray-500 uppercase tracking-wider">
                      item
                    </th>
                    <th className="px-6 py-3 bg-gray-50 text-left text-xs leading-4 font-medium text-gray-500 uppercase  tracking-wider">
                      Name
                    </th>
                    <th className="px-6 py-3 bg-gray-50 text-xs leading-4 font-medium text-gray-500 uppercase text-right tracking-wider">
                      Actions
                    </th>
                  </tr>
                </thead>
                <tbody className="bg-white divide-y divide-gray-200">
                  {category?.map((c, i) => {
                    return (
                      <tr key={c._id} className="hover:bg-slate-200">
                        <td className="px-6 py-4 whitespace-no-wrap">
                          {i + 1}
                        </td>
                        <td className="px-6 py-4 whitespace-no-wrap">
                          {c?.name}
                        </td>
                        <td className="px-6 py-4 whitespace-no-wrap flex justify-end">
                          <button
                            onClick={() => {
                              toggleEditDialogBox();
                              setUpdatedName(c.name);
                              setSelected(c);
                            }}
                            className="bg-blue-500 hover:bg-blue-700 mx-2 text-white font-bold py-2 px-4 rounded"
                          >
                            Edit
                          </button>
                          <button
                            onClick={() => {
                              handleDelete(c._id);
                            }}
                            className="bg-red-500 hover:bg-red-700 text-white font-bold py-2 px-4 rounded"
                          >
                            Delete
                          </button>
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            </div>
          </main>
        </div>
      </div>
    </Layout>
  );
};

export default CreateCategory;
