import React, { useEffect, useState } from "react";
import Layout from "../../components/Layouts/Layout";
import AdminMenu from "../../components/Layouts/AdminMenu";
import toast from "react-hot-toast";
import axios from "axios";
import { Link } from "react-router-dom";

const Products = () => {
  const [products, setProducts] = useState([]);

  //get All Products
  const getAllProducts = async (req, res) => {
    try {
      const { data } = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/product/get-product`
      );
      setProducts(data.products);
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong");
    }
  };

  //lifeCycle methods
  useEffect(() => {
    getAllProducts();
  }, []);
  return (
    <Layout title={"Admin - Products"}>
      <div className=" container">
        <div className=" grid grid-cols-10">
          <div className=" col-span-2">
            <AdminMenu></AdminMenu>
          </div>
          {/* content */}

          <main className=" col-span-8 w-full p-4">
            <div className="container  mx-auto p-6">
              {/* header */}
              <div className="  shadow-lg flex rounded-lg justify-between items-center px-6 py-4 mb-2">
                <h1 className="text-2xl w-full  font-semibold">
                  Manage Products{" "}
                </h1>
                {/* Select Category */}
                <div
                  id="search-box"
                  className=" w-1/2 relative container mx-auto "
                >
                  <input
                    type="text"
                    // onFocus={searchBarFocus}
                    placeholder="Select by Category..."
                    className="w-full px-4 py-1 my-1 outline outline-gray-300 outline-2  rounded text-gray-700 focus:outline-sky-400"
                    // value={category}
                    onChange={(e) => {
                      //   setCategory(e.target.value);
                    }}
                  />
                  <div
                    id="search-list"
                    className="absolute overflow-hidden hidden z-10 mt-2 bg-sky-200 shadow-md rounded w-full"
                  >
                    <ul>
                      {/* {categories?.map((c) => { */}
                      {/* return ( */}
                      <li
                        id="searchIDs"
                        // key={c._id}
                        onClick={() => {
                          //   setCategory(c.name);
                          //   searchBarFocus();
                        }}
                        className="px-4 py-2 cursor-pointer hover:bg-sky-600 hover:text-white"
                      >
                        {/* {c.name} */}
                      </li>
                      {/* ); */}
                      {/* })} */}
                    </ul>
                  </div>
                </div>
                {/* Select category end */}
              </div>
              {/* content */}
              <div className="bg-stone-100 mt-6 grid grid-cols-1 sm:grid-cols-2 md:grid-cols-2 lg:grid-cols-3 gap-4">
                {products.map((p) => {
                  //   console.log(p);
                  return (
                    <Link
                      key={p._id}
                      className="my-3 mx-10 scale-90"
                      to={`../admin/product/${p.slug}`}
                    >
                      <div
                        style={{ width: "310px" }}
                        className="bg-white max-w-xs rounded  shadow-md pb-4 group hover:shadow-lg shadow-gray-600 hover:shadow-gray-500 transition relative"
                      >
                        <div
                          style={{ minHeight: "300px" }}
                          className="  w-full relative flex justify-center items-center mt-2"
                        >
                          <img
                            style={{ width: "100%", height: "95%" }}
                            className="admin_product_img rounded absolute shadow-gray-400 mt-3 scale-90 group-hover:scale-100 group-hover:-top-"
                            src={`${process.env.REACT_APP_API}/api/v1/product/get-product-photo/${p._id}`}
                            alt={`${p.name}_Picture`}
                          />
                          <div className="bg-slate-700 z-10 absolute rounded-full text -bottom-4">
                            <p className="text-white px-3 py-1">
                              # {p.category.name}
                            </p>
                          </div>
                        </div>

                        <div className="px-4  mt-5 space-y-3 ">
                          {/* name / description / category*/}
                          <div className="mt-6 text-center ">
                            <div className="font-bold text-xl mb-2">
                              {p.name}
                            </div>
                            {/* <div className="w-full  overflow:hidden line-clamp-2">
                              <p className="text-gray-700 text-base">
                                {p.description}
                              </p>
                            </div> */}
                          </div>
                          <hr></hr>
                          {/* price / shipping / quantity */}

                          <div className=" bg-slate-600 rounded-full w-min flex items-center p-9  transition-all  left-2 -top-7 justify-center absolute group-hover:bg-sky-500 shadow-gray-400 shadow-md ">
                            <span className="rounded transition  -rotate-12 font-semibold text-white absolute group-hover:text-base">
                              ${p.price}
                            </span>
                          </div>

                          <div className=" flex text-center items-center justify-between py-1 ">
                            <span className="inline-block w-min whitespace-nowrap bg-stone-200 rounded px-4 py-2 font-semibold text-gray-700 text-sm">
                              Shipping : {p.Shipping ? "Yes" : "No"}
                            </span>
                            <span className="inline-block w-min whitespace-nowrap bg-stone-200 rounded px-4 py-2 font-semibold text-gray-700 text-sm">
                              Quantity : {p.quantity}
                            </span>
                          </div>
                          {/* </div> */}
                        </div>
                      </div>
                    </Link>
                  );
                })}
              </div>
              {/* content start */}

              {/* new */}
            </div>
          </main>
        </div>
      </div>
    </Layout>
  );
};

export default Products;
