import React, { useEffect, useState } from "react";
import { useNavigate, useLocation } from "react-router-dom";

const Spinner = ({ path = "login" }) => {
  const [count, setCount] = useState(3);
  const navigate = useNavigate();
  const location = useLocation();

  useEffect(() => {
    // console.log(location.pathname);
    const interval = setInterval(() => {
      setCount((prevValue) => --prevValue);
    }, 1000);
    count === 0 &&
      navigate(`/${path}`, {
        state: location.pathname,
      });
    return () => {
      clearInterval(interval);
    };
  }, [count, navigate, location, path]);

  return (
    <>
      <div className="min-h-screen flex flex-col items-center justify-center animate-pulse">
        <h1 className=" text-xl mb-5 text-sky-500 ">
          Redirecting to you in {count} seconds . . .
        </h1>
        <div className="spinner-border border-double h-12 w-12 border-0 border-r-4 border-l-4 border-sky-500 rounded-full  animate-spin"></div>
      </div>
    </>
  );
};

export default Spinner;
