/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState } from "react";
import Layout from "../../components/Layouts/Layout";
import { useNavigate } from "react-router-dom";
import toast from "react-hot-toast";
import axios from "axios";

const ForgotPassword = () => {
  const navigate = useNavigate();

  const [email, setEmail] = useState("");
  const [newPassword, setNewPassword] = useState("");
  const [question, setQuestion] = useState("");

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const res = await axios.post(
        `${process.env.REACT_APP_API}/api/v1/auth/forgot_password`,
        { email, question, newPassword }
      );
      if (res.data && res.data.success) {
        toast.success(res.data.message);
        console.log("login success");

        navigate("/");
      } else {
        console.log("login failed");
        toast.error(res.data.message);
      }
    } catch (error) {
      console.log(error);
      toast.error("Something went Wrong");
    }
  };
  return (
    <Layout title={"ForgotPassword"}>
      <div className=" min-h-screen flex items-center justify-center">
        <div className=" bg-zinc-200 p-8 rounded shadow-md w-96">
          <h1 className="text-2xl font-semibold mb-6">Forgot Password !</h1>
          <form onSubmit={handleSubmit}>
            {/* email */}
            <div className="mb-4">
              <label
                htmlFor="email"
                className="block text-gray-700 font-bold mb-2"
              >
                Email
              </label>
              <input
                type="email"
                id="email"
                name="email"
                className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:border-sky-500"
                placeholder="example@mail.com"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
              />
            </div>
            {/* Question */}
            <div className="mb-4">
              <label
                htmlFor="question"
                className="block text-gray-700 font-bold mb-2"
              >
                Name of our pet
              </label>
              <input
                type="text"
                id="question"
                name="question"
                className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:border-sky-500"
                placeholder="Tuffy , Zoro"
                value={question}
                onChange={(e) => setQuestion(e.target.value)}
                required
              />
            </div>
            {/* New Password */}
            <div className="mb-4">
              <label
                htmlFor="newPassword"
                className="block text-gray-700 font-bold mb-2"
              >
                New Password
              </label>
              <input
                type="password"
                id="newPassword"
                name="newPassword"
                className="w-full px-3 py-2 border rounded-lg focus:outline-none focus:border-sky-500"
                placeholder="********"
                value={newPassword}
                onChange={(e) => setNewPassword(e.target.value)}
                required
              />
            </div>
            <button
              type="submit"
              className="w-full bg-blue-500 text-white px-4 py-2 rounded-lg hover:bg-blue-600 active:bg-sky-400 active:scale-95"
            >
              Reset Password
            </button>
          </form>
          <div className="mt-4 flex justify-evenly items-center">
            <p className="text-sm text-gray-600">Remember your password? </p>
            <a
              onClick={() => navigate("/login")}
              className=" text-lg font-regular text-blue-600 hover:text-sky-500 active:text-purple-500 cursor-pointer"
            >
              Log In
            </a>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default ForgotPassword;
