import React from "react";
import Layout from "../../components/Layouts/Layout";
import AdminMenu from "../../components/Layouts/AdminMenu";
import { useAuth } from "../../context/auth";

const AdminDashboard = () => {
  const [auth] = useAuth();
  return (
    <Layout title={"Admin - Dashboard"}>
      <div className=" container">
        <div className=" grid grid-cols-10">
          <div className="col-span-2  bg-green-500">
            <AdminMenu></AdminMenu>
          </div>
          <main className="col-span-8 p-4">
            <div className="bg-slate-200  w-2/3 lg:w-1/3 m-auto p-8 my-16 rounded-lg shadow-lg">
              <div className="flex justify-center  -mt-14">
                <img
                  src="/images/man1.png"
                  alt="Profile"
                  className="rounded-full border-4 pt-5 border-slate-200 w-60"
                />
              </div>
              <div className="text-center mt-4">
                <h1 className="text-2xl font-semibold">{auth?.user?.name}</h1>
              </div>
              <div className="mt-4">
                <p className="text-gray-700">Email: {auth?.user?.email}</p>
                <p className="text-gray-700">Phone: {auth?.user?.phone}</p>
              </div>
            </div>
          </main>
        </div>
      </div>
    </Layout>
  );
};

export default AdminDashboard;

