import React, { useEffect, useState } from "react";
import Layout from "../components/Layouts/Layout";
import axios from "axios";
import { Link, useNavigate, useParams } from "react-router-dom";
import { useCart } from "../context/cart";
import toast from "react-hot-toast";

const ProductDetails = () => {
  const navigate = useNavigate();
  const [cart, setCart] = useCart();

  const params = useParams();
  const [product, setProduct] = useState({});
  const [relatedProducts, setRelatedProducts] = useState([]);

  useEffect(() => {
    if (params?.slug) getProduct();
  }, [params.slug]);

  // get product
  const getProduct = async () => {
    try {
      // console.log(params);
      const { data } = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/product/get-product/${params.slug}`
      );
      setProduct(data?.product);
      getSimilarProducts(data?.product._id, data?.product.category._id);
    } catch (error) {
      console.log(error);
    }
  };

  const getSimilarProducts = async (pid, cid) => {
    try {
      const { data } = await axios.get(
        `${process.env.REACT_APP_API}/api/v1/product/product-related/${pid}/${cid}`
      );
      setRelatedProducts(data?.products);
    } catch (error) {
      console.log(error);
    }
  };

  //   src={`${process.env.REACT_APP_API}/api/v1/product/get-product-photo/${product._id}`}

  return (
    <Layout>
      <div className="max-w-7xl mx-auto mt-10 px-4 sm:px-6 lg:px-8">
        <div className="flex flex-col md:flex-row -mx-4">
          <div className="md:flex-1 px-4">
            <div className="h-64 md:h-80 rounded-lg bg-gray-100 mb-4 flex items-center justify-center">
              <img
                src={`${process.env.REACT_APP_API}/api/v1/product/get-product-photo/${product?._id}`}
                className=" rounded-lg h-full"
                alt="helo"
              ></img>
            </div>
          </div>
          <div className="md:flex-1 px-4">
            <h2 className="mb-2 leading-tight tracking-tight font-bold text-gray-800 text-2xl md:text-3xl">
              {product?.name}
            </h2>
            <p className="text-gray-500 text-sm"># {product?.category?.name}</p>

            <div className="flex items-center space-x-4 my-4">
              <div>
                <div className="rounded-lg bg-gray-100 flex py-2 px-3">
                  <span className="text-sky-400 mr-1 mt-1">$</span>
                  <span className="font-bold text-sky-500 text-3xl">
                    {product?.price}
                  </span>
                </div>
              </div>
              <div className="flex-1">
                <p className="text-green-500 text-xl font-semibold">Save 12%</p>
                <p className="text-gray-400 text-sm">Inclusive of all Taxes.</p>
              </div>
            </div>

            <p className="text-gray-500">{product?.description}</p>

            <div className="flex py-4 space-x-4">
              <div
                type="button"
                className=" px-6 py-4 font-semibold rounded-xl bg-gray-300  text-gray-700"
              >
                In Stock - {product.quantity}
              </div>
              <button
                type="button"
                onClick={() => {
                  setCart([...cart, product]);
                  localStorage.setItem(
                    "cart",
                    JSON.stringify([...cart, product])
                  );
                  toast.success("Item Added to cart");
                }}
                className=" px-6 py-4 font-semibold rounded-xl bg-sky-500 hover:bg-sky-400 text-white"
              >
                Add to Cart
              </button>
            </div>
          </div>
        </div>
      </div>
      {/* peoduct details end */}
      <div className="my-8 px-20">
        {relatedProducts.length > 1 ? (
          <h3 className="text-xl font-semibold mb-4">Similar Products</h3>
        ) : (
          <h5 className="text-lg font-semibold mb-4">
            No similar Products Found
          </h5>
        )}
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
          {/* simlar product */}
          {relatedProducts.map((r) => {
            return (
              <div
                key={r?._id}
                onClick={() => {
                  navigate(`../product/${r?.slug}`);
                }}
                className="bg-gray-200 mt-5 rounded-lg shadow-lg p-4 relative group hover:bg-gray-100 cursor-pointer transition"
              >
                <img
                  src={`${process.env.REACT_APP_API}/api/v1/product/get-product-photo/${r?._id}`}
                  alt="Similar Product 1"
                  className="w-full max-h-52 object-cover mb-6 group-hover:scale-95 transition"
                />
                <div className="absolute bottom-3 w-full left-0   flex items-center px-4">
                  <h4 className="text-lg font-semibold group-hover:text-sky-400 transition">
                    {r?.name}
                  </h4>
                  <div className="px-10 py-5 flex items-center justify-center bg-gray-600 group-hover:bg-sky-400 absolute right-4  rounded-full  shadow transition">
                    <p className="absolute text-gray-50">${r?.price}</p>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>

      {/* {JSON.stringify(relatedProducts, null, 4)} */}
    </Layout>
  );
};

export default ProductDetails;
